import React, { useContext, useState } from 'react';
import { api_context } from '../Context/Api';
import { Link } from 'react-router-dom';

export function Navbar() {
    const { createRipple, s_val, onChange } = useContext(api_context);

    return (
        <div className='navbar'>
            <Link to={`/search/${s_val}`} id='to_search' />
            <Link to={`/`} id='to_slash' />
            <button className='nav-icon' onClick={e => {
                createRipple(e);
                document.getElementById("to_slash").click();
            }}>
                <h2 className='c-white'><span>Pexeleseone </span></h2>
                {/* <svg xmlns="http://www.w3.org/2000/svg" fill='currentColor' viewBox="0 0 576 512">
                    <path d="M575.8 255.5c0 18-15 32.1-32 32.1h-32l.7 160.2c0 2.7-.2 5.4-.5 8.1V472c0 22.1-17.9 40-40 40H456c-1.1 0-2.2 0-3.3-.1c-1.4 .1-2.8 .1-4.2 .1H416 392c-22.1 0-40-17.9-40-40V448 384c0-17.7-14.3-32-32-32H256c-17.7 0-32 14.3-32 32v64 24c0 22.1-17.9 40-40 40H160 128.1c-1.5 0-3-.1-4.5-.2c-1.2 .1-2.4 .2-3.6 .2H104c-22.1 0-40-17.9-40-40V360c0-.9 0-1.9 .1-2.8V287.6H32c-18 0-32-14-32-32.1c0-9 3-17 10-24L266.4 8c7-7 15-8 22-8s15 2 21 7L564.8 231.5c8 7 12 15 11 24z" />
                </svg>
                <p>Home</p> */}
            </button>
            <form className='search-nav' onSubmit={e => {
                e.preventDefault();
                document.getElementById("to_slash").click();
                setTimeout(() => document.getElementById("to_search").click(), 500)
            }}>
                <input type="search" value={s_val} onChange={onChange} placeholder='Search here' />
                <button type='submit' onClick={createRipple}>Search</button>
            </form>
        </div>
    )
}
