import React, { useState, useEffect, useContext } from 'react';
import { Image as Image_component } from './Image';
import InfiniteScroll from "react-infinite-scroll-component";
import { Loader } from './Loader';
import { loader } from '../Context/Images';
import { api_context } from '../Context/Api';

export function Image_Container() {
    const [images, setImages] = useState({ results: [] });
    const [page, setPage] = useState(1);
    const [is_online, setIs_online] = useState(true);
    const [loading, setLoading] = useState(true);
    const { unsplash, createRipple } = useContext(api_context);
    const [key, setKey] = useState(0);

    // const client = createClient("Vqsm4pb6gZVyC4bTtnj1zwvPb0C4u2DeHOc6Guc1V12JC8azJkMRWFya");

    async function get_images() {
        document.title = "Pexelseone | Best Quality Images for free"
        setLoading(true);
        const imgs = await unsplash.photos.list({ page, perPage: 40 }).then(data => {
            return data;
        }).catch(_err => {
            setLoading(false)
            return false;
        });
        if (!imgs)
            return setIs_online(false)
        setImages(imgs.response);
        setLoading(false);
    }

    async function getNew_images() {
        const imgs = await unsplash.photos.list({ page: page + 1, perPage: 40 }).then(data => {
            return data;
        }).catch(_err => {
            return false;
        });
        if (!imgs)
            return setIs_online(false)
        setPage(page + 1);
        setImages({ ...images, results: images.results.concat(imgs.response.results) });
    }

    useEffect(() => {
        get_images()
        // eslint-disable-next-line
    }, [])

    return (
        <>
            {is_online ? <InfiniteScroll loader={<Loader />} endMessage={images.results.length === 0 ? "No Results" : "No more Results"} next={getNew_images} hasMore={images.results.length !== images.total} dataLength={images.results.length}>
                <h2 className='brand-name'><span>Pexeleseone </span>- Get some quality images for free</h2>
                <div className='image-container'>
                    {images.results.length !== 0 &&
                        images.results.map(image => {
                            const img = new Image();
                            const src = image.urls.small ? image.urls.small : image.urls.full;
                            img.src = src;
                            // eslint-disable-next-line
                            return <Image_component key={image.id} id={image.id} download_link={image.urls.full} description={image.description} alt_description={image.alt_description} width={img.width} src={src} />
                        })}
                </div>
            </InfiniteScroll> : <div className='image-container'>
                <div className='offline'>
                    <h2>Please Connect to the internet</h2>
                    <button className='btn-offline' onClick={e => {
                        createRipple(e);
                        get_images();
                    }}>{loading ? <img src={loader} alt='' /> : "Retry"}</button>
                </div>
            </div>
            }
        </>
    );
}