import React, { useContext, useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { Navbar } from './Navbar';
import { api_context } from '../Context/Api';
import InfiniteScroll from 'react-infinite-scroll-component';
import { Loader } from './Loader';
import { Image as ImageComponent } from './Image';
import { loader } from '../Context/Images';

export function Search() {
    const params = useParams();
    const [images, setImages] = useState({ results: [] });
    const [page, setPage] = useState(0);
    const [is_online, setIs_online] = useState(true);
    const { unsplash, createRipple, setSval } = useContext(api_context);

    useEffect(() => {
        setSval(params.keyword);
        search_images();
    }, []);

    async function search_images() {
        document.title = "Pexelseone | Search Results for \`" + params.keyword + "\`";
        const imgs = await unsplash.search.getPhotos({ query: params.keyword, page, perPage: 40 }).then(data => {
            return data;
        }).catch(_err => {
            return false;
        });
        if (!imgs)
            return setIs_online(false)
        setImages(imgs.response)
    }

    async function getNew_images() {
        if (params.keyword === "")
            return
        const imgs = await unsplash.search.getPhotos({ query: params.keyword, page: page + 1, perPage: 40 }).then(data => {
            return data;
        }).catch(_err => {
            return false;
        });
        if (!imgs)
            return setIs_online(false)
        setPage(page + 1);
        setImages({ ...images, results: images.results.concat(imgs.response.results) });
    }

    return (
        <>
            <Navbar />
            {is_online ? <InfiniteScroll endMessage={images.results.length === 0 ? "No Results" : "No more Results"} loader={<Loader />} next={getNew_images} hasMore={images.results.length !== images.total} dataLength={images.results.length}>
                <h1 className='f-inter text-center my-1'>{"Search results for \`" + params.keyword + "\`"}</h1>
                <div className='image-container'>
                    {images.results.length !== 0 &&
                        images.results.map(image => {
                            const img = new Image();
                            const src = image.urls.small ? image.urls.small : image.urls.full;
                            img.src = src;
                            return <ImageComponent key={image.id} id={image.id} download_link={image.urls.full} description={image.description} alt_description={image.alt_description} width={img.width} src={src} />
                        })}
                </div>
            </InfiniteScroll> : <div className='image-container'>
                <div className='offline'>
                    <h2>Please Connect to the internet</h2>
                    <button className='btn-offline' onClick={e => {
                        createRipple(e);
                        document.getElementById("to_slash").click();
                        setTimeout(() => document.getElementById("to_search").click(), 500)
                    }}>Retry</button>
                </div>
            </div>
            }
        </>
    )
}
